<template>
  <div class="gis">
    <!-- 地图 -->
    <div id="map" />
    <!-- 信息图层 -->
    <div class="message elevation-11" v-if="message.id">
      <div class="name">{{ message.name }} - {{ message.capsuleLocation }}</div>
      <div class="content">
        <div class="content_handle" v-if="message.common">
          <div class="content_handle_icon">
            <v-icon color="#ffffff" size="30">
              mdi-air-conditioner
            </v-icon>
          </div>
          <div class="content_handle_auto">
            <span class="content_handle_auto_close">
              自动
            </span>
            <v-switch
              inset
              light
              dense
              color="#32c95c"
              :true-value="1"
              :false-value="0"
              class="ma-0 pa-0 ml-5"
              @change="setAuto(message)"
              v-model="message.value[`auto`]"
            />
            <span class="content_handle_auto_open">
              手动
            </span>
          </div>
          <div class="content_handle_open">
            <span class="content_handle_auto_close">
              关
            </span>
            <v-switch
              inset
              light
              dense
              color="#32c95c"
              :true-value="1"
              :false-value="0"
              class="ma-0 pa-0 ml-5"
              @change="setOpen(message)"
              v-model="message.value[`open`]"
            />
            <span class="content_handle_auto_open">
              开
            </span>
          </div>
        </div>
        <div class="content_handle" v-else-if="message.auto">
          <div class="content_handle_icon">
            <v-icon color="#ffffff" size="30">
              mdi-air-conditioner
            </v-icon>
          </div>
          <div class="content_handle_auto">
            <span class="content_handle_auto_close">
              自动
            </span>
            <v-switch
              inset
              light
              dense
              color="#32c95c"
              :true-value="1"
              :false-value="0"
              class="ma-0 pa-0 ml-5"
              @change="setAuto(message)"
              v-model="message.value[`auto`]"
            />
            <span class="content_handle_auto_open">
              手动
            </span>
          </div>
        </div>
        <div class="content_handle" v-else-if="message.open">
          <div class="content_handle_icon">
            <v-icon color="#ffffff" size="30">
              mdi-air-conditioner
            </v-icon>
          </div>
          <div class="content_handle_open">
            <span class="content_handle_auto_close">
              关
            </span>
            <v-switch
              inset
              light
              dense
              color="#32c95c"
              :true-value="1"
              :false-value="0"
              class="ma-0 pa-0 ml-5"
              @change="setOpen(message)"
              v-model="message.value[`open`]"
            />
            <span class="content_handle_auto_open">
              开
            </span>
          </div>
        </div>
        <div class="content_handle" v-else-if="message.smoke">
          <div
            :class="
              !message.value[`smoke`]
                ? `content_handle_icon`
                : `content_handle_deep_icon`
            "
          >
            <v-icon :size="30" :color="`#ffffff`">
              mdi-air-conditioner
            </v-icon>
          </div>
          <div class="content_handle_status">
            <span
              class="content_handle_status_have"
              v-if="message.value[`smoke`]"
            >
              有烟雾
            </span>
            <span class="content_handle_status_none" v-else>
              无烟雾
            </span>
          </div>
          <div class="content_handle_hint">
            当前状态
          </div>
        </div>
        <div v-else class="content_property">
          <div
            :key="index"
            :class="`content_property_source`"
            v-for="(n, name, index) of JSON.parse(message.threshold)"
          >
            <div class="content_property_source_container">
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <div
                    class="content_property_source_container_circular"
                    v-bind="attrs"
                    v-on="on"
                  >
                    <v-progress-circular
                      :value="message.value[name]"
                      :color="`#8ae8b6`"
                      :rotate="270"
                      :size="100"
                      :width="7"
                    >
                      {{ message.value[name] }}
                    </v-progress-circular>
                  </div>
                </template>
                <span>
                  {{ name }}
                </span>
              </v-tooltip>
              <div class="content_property_source_container_range">
                <div class="content_property_source_container_range_down">
                  <v-icon light color="#33aefd" size="17">
                    mdi-arrow-down-thick
                  </v-icon>
                  <span
                    class="content_property_source_container_range_down_count"
                  >
                    {{ n[1] }}
                  </span>
                </div>
                <div class="content_property_source_container_range_up">
                  <v-icon light color="#fc5a7c" size="17">
                    mdi-arrow-up-thick
                  </v-icon>
                  <span
                    class="content_property_source_container_range_up_count"
                  >
                    {{ n[0] }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { loadModules } from "esri-loader";
import { mapGetters } from "vuex";
export default {
  name: "gis",
  data: () => ({
    map: {
      graphics: {
        add: () => {},
        removeAll: () => {},
      },
    },
    message: {},
    mapGraphic: () => {},
  }),
  computed: {
    // 数据仓库
    ...mapGetters(["myMarker"]),
  },
  watch: {
    myMarker: {
      handler(value) {
        // 数据整合
        const list = value.map((res) => {
          return {
            // 基础数据
            ...res,
            // 两者功能都兼具
            common:
              // 打开关闭
              `open` in JSON.parse(res.threshold) &&
              // 自动手动
              `auto` in JSON.parse(res.threshold),
            // 打开关闭
            open: `open` in JSON.parse(res.threshold),
            // 自动手动
            auto: `auto` in JSON.parse(res.threshold),
            // 烟雾感应
            smoke: `smoke` in JSON.parse(res.threshold),
          };
        });
        // 实例创建
        this.create(list);
      },
      deep: true,
    },
  },
  mounted() {
    this.myMap();
  },
  methods: {
    myMap() {
      // 资源配置
      const options = {
        url: "https://js.arcgis.com/4.15/",
        css: "https://js.arcgis.com/4.15/esri/themes/light/main.css",
      };
      // 按需模块加载
      loadModules(
        [
          "esri/Map",
          "esri/Graphic",
          "esri/views/MapView",
          "esri/widgets/BasemapToggle",
        ],
        options
      ).then(([Map, Graphic, MapView, BasemapToggle]) => {
        // 地图类型配置
        const map = new Map({
          basemap: "osm",
        });
        // 地图渲染配置
        const view = new MapView({
          map,
          zoom: 15,
          container: "map",
          center: [116.4627, 40.1211],
        });
        // 地图切换配置
        const toggle = new BasemapToggle({
          view,
          nextBasemap: "hybrid",
        });
        // 地图实例添加切换样式
        view.ui.add(toggle, "top-right");
        // 地图视图监听图层点击事件的回调
        view.on("click", (e) => {
          // 记录点击坐标
          const point = {
            x: e.x,
            y: e.y,
          };
          // 利用检索功能, 判断所点击位置上是否含有创建添加的图形
          view.hitTest(point).then((res) => {
            // 判断是否点击在图形上
            if (!res.results[0]) {
              // 数据置空
              this.message = {};
            } else {
              // 获取绑定的实际数据
              this.message = res.results[0]["graphic"]["attributes"];
            }
          });
        });
        // 地图视图监听图层创建完毕事件的回调
        view.on("layerview-create", () => {
          // 储存绘制图形的构造函数
          this.mapGraphic = Graphic;
          // 储存实例
          this.map = view;
          // 初始化
          this.create([]);
        });
      });
    },
    create(list) {
      // 清空所有图层
      this.map.graphics.removeAll();
      // 几何位置配置
      const polyline = {
        type: "polyline",
        paths: [
          [116.4615, 40.1059],
          [116.4618, 40.1158],
          [116.4621, 40.1211],
          [116.4627, 40.1265],
          [116.4669, 40.1323],
          [116.4674, 40.1335],
          [116.4676, 40.1362],
        ],
      };
      // 几何样式配置
      const lineSymbol = {
        width: 6,
        type: "simple-line",
        color: [230, 81, 0],
      };
      // 实例几何图形
      const polylineGraphic = new this.mapGraphic({
        symbol: lineSymbol,
        geometry: polyline,
        attributes: {
          Age: "2020",
          Name: "****",
          Owner: "TransCanada",
          Message: "智慧管廊信息可视化",
        },
        popupTemplate: {
          title: "智慧管廊 - 综合舱地理信息：",
          content: [
            {
              type: "fields",
              fieldInfos: [
                {
                  fieldName: "Age",
                },
                {
                  fieldName: "Name",
                },
                {
                  fieldName: "Owner",
                },
                {
                  fieldName: "Message",
                },
              ],
            },
          ],
        },
      });
      // 地图实例添加几何图形
      this.map.graphics.add(polylineGraphic);
      // 循环遍历标记
      for (const res of list) {
        // 点坐标配置
        const geometry = {
          type: "point",
          longitude: 116.4615,
          latitude: 40.106 + Math.random() / 100,
        };
        // 符号配置
        const symbol = {
          type: "simple-marker",
          color: "white",
          size: 12,
          outline: {
            width: 2,
            color: "red",
          },
        };
        // 绘制图形并用创建的符号来呈现点标记
        const markGraphic = new this.mapGraphic({
          symbol,
          geometry,
          attributes: res,
        });
        // 地图图形化实例添加新图形
        this.map.graphics.add(markGraphic);
      }
    },
    setAuto(i) {
      // 参数整合
      const params = {
        id: i.id,
        key: "auto",
        value: i.value["auto"],
      };
      // 状态设置
      this.myStatus(params);
    },
    setOpen(i) {
      // 参数整合
      const params = {
        id: i.id,
        key: "open",
        value: i.value["open"],
      };
      // 状态设置
      this.myStatus(params);
    },
    myStatus(params) {
      this.$axios
        .post("/dataManage/instruction/updateStatus", {
          ...params,
        })
        .then((res) => {
          if (res) {
            // 成功提示
            this.$notice.success("状态已设置成功");
          }
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.gis {
  height: 100%;
  position: relative;
  background-image: linear-gradient(179deg, #d9e8ff 0%, #f7f7f8 27%);
  #map {
    height: 100%;
  }
  .message {
    top: 15px;
    right: 100px;
    display: flex;
    position: absolute;
    flex-direction: column;
    background-color: #f7f7f8;
    .name {
      display: flex;
      font-size: 13px;
      color: #555555;
      font-weight: bold;
      padding: 17px 21px;
      align-items: center;
      justify-content: center;
      border-bottom: 1px solid rgba(153, 153, 153, 0.3);
    }
    .content {
      flex: 1;
      display: flex;
      font-size: 13px;
      min-width: 270px;
      padding: 17px 21px;
      .content_handle {
        flex: 1;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        .content_handle_icon {
          width: 60px;
          height: 60px;
          display: flex;
          border-radius: 50%;
          align-items: center;
          justify-content: center;
          background-color: #32c95c;
        }
        .content_handle_deep_icon {
          width: 60px;
          height: 60px;
          display: flex;
          border-radius: 50%;
          align-items: center;
          justify-content: center;
          background-color: #fc5a7c;
        }
        .content_handle_auto,
        .content_handle_open {
          display: flex;
          margin-top: 20px;
          justify-content: center;
          .content_handle_auto_close {
            font-size: 13px;
            color: #33aefd;
          }
          .content_handle_auto_open {
            font-size: 13px;
            color: #fc5a7c;
          }
        }
        .content_handle_status {
          display: flex;
          margin-top: 20px;
          align-items: center;
          justify-content: center;
          .content_handle_status_none {
            font-size: 15px;
            color: #33aefd;
          }
          .content_handle_status_have {
            font-size: 15px;
            color: #fc5a7c;
          }
        }
        .content_handle_hint {
          font-size: 15px;
          margin-top: 20px;
          color: rgb(171, 164, 164);
        }
      }
      .content_property {
        flex: 1;
        display: flex;
        .content_property_source {
          flex: 1;
          display: flex;
          .content_property_source_container {
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 15px;
            .content_property_source_container_circular {
              flex: 1;
              display: flex;
              align-items: center;
              justify-content: center;
            }
            .content_property_source_container_range {
              display: flex;
              align-content: center;
              justify-content: space-between;
              .content_property_source_container_range_down {
                display: flex;
                align-items: flex-end;
                justify-content: center;
                .content_property_source_container_range_down_count {
                  font-size: 12px;
                  color: #33aefd;
                  margin-left: 5px;
                  display: inline-block;
                }
              }
              .content_property_source_container_range_up {
                display: flex;
                align-items: flex-start;
                justify-content: center;
                .content_property_source_container_range_up_count {
                  font-size: 12px;
                  color: #fc5a7c;
                  margin-left: 5px;
                  display: inline-block;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
